/* NAV BAR */

.nav {
  background-color: greenyellow;
  margin: 0px;
  padding: 10px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  font-family: 'Francois One';
}

.logo {
  margin-left: 10px;
  margin-right: auto;
  font-weight: bold;
  font-size: 1.9em;
  font-family: 'Francois One';
  color: black;
}

.search-bar {
  margin-left: 10px;
  padding: 10px;
  margin-right: auto;
}

.nav-sub {
  display: flex;
  margin-left: auto;
  padding: 10px;
  margin-right: 10px;
  align-items: center;
}

.nav-selected {
  padding: 5px;
  color: orange;
  font-weight: bold;
  margin-right: 5px;
  font-size: 1.1em;
}

.nav-unselected {
  padding: 5px;
  color: black;
  font-weight: bold;
  margin-right: 5px;
  font-size: 1.1em;
}

.profile-nav {
  display: flex;
  width: 5%;
  height: 5%;
  border-radius: 50%;
  margin: 10px;
}

/* MAIN */

body {
  font-family: helvetica;
  background: #f1f1f1;
  font-family: Helvetica;
  height: 100%;
  margin: 0;
}

.main {
  margin-right: 1rem;
  margin-left: 1rem;
  padding: 1rem;
}

.cards {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: auto;
  grid-gap: 1.3rem;
}

.card {
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  font-family: Helvetica;
  font-size: 1.0em;
  transition: 0.3s;
  border-radius: 5%;
}

.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}

.container {
  padding: 2px 16px;
}

.card-profile {
  display: flex;
}

.card-photo {
  max-width: 100%;
  max-height: 82vh;
}

.profile-photo {
  width: 15%;
  height: 15%;
  border-radius: 50%;
  margin: 10px;
  padding: 10px;
}

.profile-card {
  width: 10%;
  height: 10%;
  border-radius: 50%;
  margin: 10px;
}

/* ACCOUNT */

.profile {
  display: flex;
}

/* POST */

/* .post {
  display: inline-block;
  position: relative;
} */

.post {
  margin-left: 3rem;
  margin-right: 3rem;
  padding: 1rem;
  text-align: center;
}

.post-one {
  align-items: center;
}

.post-image {
  max-width: 60rem;
}

.post-user {
  width: 10%;
  height: 10%;
  border-radius: 50%;
}

.post-two {
  align-items: center;
}

/* CREATE */

  /* .create h2 {
    border-bottom: 1px solid #777777;
    width: 90%;
    font-family: 'Francois One';
  } */

.create-editor {
  width: 40%;
  float: left;
}

.create-preview {
  width: 60%;
  float: right;
  margin: 0 auto;
}

.create-input {
  width: 80%;
  display: block;
  font-size: .9em;
  height: 30px;
  margin-bottom: 15px;
  border: none;
  padding: 5px;
}

.create-body-textarea {
  width: 80%;
  font-size: .9em;
  display: block;
  height: 150px;
  border: none;
  padding: 5px;
  resize: none;
}

.create-submit-button {
  margin-top: 15px;
  font-size: .9em;
  height: 30px;
}

